import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BaseComponent } from '../../../../base.component';
import { ToastrService } from 'ngx-toastr';
import { SpinnerService } from '../../../../core/spinner/spinner.service';
import { finalize } from 'rxjs/operators';
import { NumberValidator } from '../../../../validators/number.validator';
import { Machine } from './machine.model';
import { NhModalComponent } from '../../../../shareds/components/nh-modal/nh-modal.component';
import { UtilService } from '../../../../shareds/services/util.service';
import { TimekeepingConfigService } from '../timekeeping-config.service';

@Component({
    selector: 'app-timekeeping-machine',
    templateUrl: './timekeeping-machine.component.html',
    providers: [NumberValidator]
})

export class TimekeepingMachineComponent extends BaseComponent implements OnInit {
    @ViewChild('machineFormModal') machineFormModal: NhModalComponent;
    machine = new Machine();
    listMachine: Machine[] = [];
    formModel: FormGroup;
    listMachineNo = [];

    constructor(private fb: FormBuilder,
                private toastr: ToastrService,
                private numberValidator: NumberValidator,
                private spinnerService: SpinnerService,
                private utilService: UtilService,
                private timekeepingConfigService: TimekeepingConfigService) {
        super();
        for (let i = 1; i < 20; i++) {
            this.listMachineNo.push({
                id: i,
                name: i
            });
        }
    }

    ngOnInit() {
        this.search();
        this.buildForm();
    }

    onMachineFormModalShown() {
        this.utilService.focusElement('machineName');
    }

    onMachineFormModalHidden() {
        this.isUpdate = false;
        this.formModel.reset(new Machine());
    }

    search() {
        this.isSearching = true;
        this.timekeepingConfigService.searchMachine()
            .pipe(finalize(() => this.isSearching = false))
            .subscribe((result: any) => {
                this.listMachine = result;
            });
    }

    edit(machine: Machine) {
        this.isUpdate = true;
        this.formModel.patchValue(machine);
        this.machineFormModal.show();
    }

    delete(machine: Machine) {
        // swal({
        //     title: `Bn c chc chn mun xa my chm cng: "${machine.name}" khng?`,
        //     text: 'Lu  sau khi xa bn khng th ly li my chm cng ny!',
        //     type: 'warning',
        //     showCancelButton: true,
        //     confirmButtonColor: '#DD6B55',
        //     confirmButtonText: 'ng ',
        //     cancelButtonText: 'Hy b'
        // }).then(() => {
        //     this.spinnerService.show('ang xa my chm cng...');
        //     this.timekeepingConfigService.deleteMachine(machine.id)
        //         .finally(() => this.spinnerService.hide())
        //         .subscribe(result => {
        //             this.toastr.success(this.formatString(this.message.deleteSuccess, machine.name));
        //             this.search();
        //         });
        // }, () => {
        // });
    }

    save() {
        this.isSubmitted = true;
        this.machine = this.formModel.value;
        const isValid = this.utilService.onValueChanged(this.formModel, this.formErrors, this.validationMessages);

        if (isValid) {
            this.spinnerService.show('ang lu thng tin my chm cng.');
            if (this.isUpdate) {
                this.timekeepingConfigService.updateMachine(this.machine)
                    .pipe(finalize(() => {
                        this.spinnerService.hide();
                        this.isSubmitted = false;
                    }))
                    .subscribe(result => {
                        this.toastr.success(this.formatString(this.message.updateSuccess, 'my chm cng'));
                        this.search();
                        this.machineFormModal.dismiss();
                    });
            } else {
                this.timekeepingConfigService.insertMachine(this.machine)
                    .pipe(finalize(() => {
                        this.spinnerService.hide();
                        this.isSubmitted = false;
                    }))
                    .subscribe(result => {
                        this.toastr.success(this.formatString(this.message.insertSuccess, 'my chm cng'));
                        this.search();
                        this.formModel.reset(new Machine());
                    });
            }
        }
    }

    getSerialNumber() {
        const value = this.formModel.value;
        if (!value.ip) {
            this.toastr.error('Vui lng nhp a ch ip');
            this.utilService.focusElement('machineIp');
            return;
        }

        if (!value.port) {
            this.toastr.error('Vui lng nhp cng kt ni');
            this.utilService.focusElement('machinePort');
            return;
        }

        console.log('ddang layas thong tin serial number');
        this.spinnerService.show('ang ly thng tin serial number.');
        this.timekeepingConfigService.getSerial(this.formModel.value.ip, this.formModel.value.port)
            .pipe(finalize(() => this.spinnerService.hide()))
            .subscribe(result => {
                if (!result) {
                    this.toastr.error('Vui lng kim tra li a ch ip hoc cng kt ni', 'Khng th kt ni');
                    return;
                }
                this.formModel.patchValue({'serialNumber': result});
            });
    }

    private buildForm() {
        this.formErrors = this.utilService.renderFormError(['name', 'no', 'ip', 'port', 'series']);
        this.validationMessages = {
            'name': {
                'required': this.formatString('Vui lng nhp tn my chm cng'),
                'maxlength': this.formatString('Tn my chm cng khng c php vt qu 250 k t.')
            },
            'no': {
                'required': this.formatString('Vui lng chn s my chm cng')
            },
            'ip': {
                'required': this.formatString('Vui lng nhp a ch IP'),
                'maxlength': this.formatString('a ch IP khng c php vt qu 100 k t.')
            },
            'port': {
                'required': this.formatString('Vui lng nhp Port'),
                'isValid': this.formatString('Port phi l s.')
            },
            'serialNumber': {
                'required': this.formatString('Vui lng chn s seri')
            },
            'registerNumber': {
                'required': this.formatString('Vui lng chn s ng k')
            }
        };
        this.formModel = this.fb.group({
            'id': [this.machine.id],
            'name': [this.machine.name, [
                Validators.required,
                Validators.maxLength(250)
            ]],
            'no': [this.machine.no, [
                Validators.required
            ]],
            'ip': [this.machine.ip, [
                Validators.required,
                Validators.maxLength(100)
            ]],
            'port': [this.machine.port, [
                Validators.required,
                this.numberValidator.isValid
            ]],
            'serialNumber': [this.machine.serialNumber, [
                Validators.required
            ]],
            'isActive': [this.machine.isActive]
        });
        this.formModel.valueChanges.subscribe(data =>
            this.utilService.onValueChanged(this.formModel, this.formErrors, this.validationMessages));
        this.utilService.onValueChanged(this.formModel, this.formErrors, this.validationMessages);
    }
}
